.icon-left {
    position: absolute;
    top: 10px;
    left: 10px;
    }
    
    .icon-right {
    position: absolute;
    bottom: 10px; 
    right: 10px;
    }
    
    .season-display.winter i {
        color: blue;
    }
    
    .season-display.summer i {
        color: red;
    }
     
    
    .season-display {
        display:flex;
        justify-content: center;
        align-items: center;
        height: 100vh;
    }
    
    .winter {
        background-color:rgb(34, 89, 138);
    
    }
    
    .summer {
        background-color: orange;
    }
